@use '../../styles/theme.scss';

:host {
  display: block;
  height: 100%;
  background: var(--project-bg-secondary);
}

.gantt-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 24px;
  overflow: hidden;

  .gantt-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
    padding-bottom: 16px;
    border-bottom: 2px solid var(--project-border-color);

    .header-left {
      h1 {
        margin: 0 0 8px 0;
        font-size: 28px;
        font-weight: 700;
        color: var(--project-text-primary);
        background: var(--project-gradient-primary);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
      }

      p {
        margin: 0;
        color: var(--project-text-secondary);
        font-size: 14px;
      }
    }

    .header-right {
      display: flex;
      gap: 16px;
      align-items: center;

      .view-mode-buttons {
        display: flex;
        gap: 8px;

        button {
          display: flex;
          align-items: center;
          gap: 6px;
          padding: 8px 16px;
          background: var(--project-bg-primary);
          color: var(--project-text-primary);
          border: 1px solid var(--project-border-color);
          border-radius: 8px;
          transition: all 0.3s ease;

          &.active {
            background: var(--project-gradient-primary);
            color: white;
            border-color: var(--project-primary);
            box-shadow: 0 4px 12px var(--project-shadow);
          }

          &:hover:not(.active) {
            background: var(--project-gradient-card);
            border-color: var(--project-primary);
            transform: translateY(-2px);
          }

          mat-icon {
            font-size: 18px;
            width: 18px;
            height: 18px;
          }
        }
      }

      .zoom-controls {
        display: flex;
        gap: 4px;
        padding: 4px;
        background: var(--project-bg-primary);
        border-radius: 8px;
        border: 1px solid var(--project-border-color);

        button {
          color: var(--project-primary);

          &:hover {
            background: var(--project-gradient-card);
          }
        }
      }
    }
  }

  // Loading, Error, Empty states
  .loading-container,
  .error-container,
  .empty-state {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 80px 20px;

    mat-icon {
      font-size: 120px;
      width: 120px;
      height: 120px;
      color: var(--project-text-disabled);
      margin-bottom: 24px;
      opacity: 0.5;
    }

    h2 {
      margin: 0 0 12px 0;
      font-size: 24px;
      font-weight: 700;
      color: var(--project-text-primary);
    }

    p {
      margin: 0 0 24px 0;
      color: var(--project-text-secondary);
      font-size: 16px;
    }
  }

  // Main gantt content
  .gantt-content {
    flex: 1;
    background: var(--project-bg-primary);
    border-radius: 16px;
    border: 1px solid var(--project-border-color);
    overflow: hidden;
    display: flex;
    flex-direction: column;

    .gantt-wrapper {
      display: flex;
      flex: 1;
      overflow: hidden;

      // Left side - Task list
      .task-list {
        width: 320px;
        min-width: 320px;
        border-right: 2px solid var(--project-border-color);
        display: flex;
        flex-direction: column;
        background: var(--project-bg-secondary);

        .task-list-header {
          height: 60px;
          display: flex;
          align-items: center;
          padding: 0 20px;
          background: var(--project-gradient-primary);
          color: white;
          font-weight: 700;
          font-size: 16px;
          border-bottom: 2px solid var(--project-primary-dark);

          .header-cell {
            flex: 1;
          }
        }

        .task-list-body {
          flex: 1;
          overflow-y: auto;

          &::-webkit-scrollbar {
            width: 8px;
          }

          &::-webkit-scrollbar-track {
            background: var(--project-bg-secondary);
          }

          &::-webkit-scrollbar-thumb {
            background: var(--project-primary);
            border-radius: 4px;

            &:hover {
              background: var(--project-primary-dark);
            }
          }

          .task-row {
            display: flex;
            align-items: center;
            padding: 12px 20px;
            border-bottom: 1px solid var(--project-border-color);
            transition: background 0.2s ease;

            &:hover {
              background: var(--project-gradient-card);
            }

            .task-info {
              flex: 1;
              display: flex;
              flex-direction: column;
              gap: 8px;

              .task-name {
                display: flex;
                align-items: center;
                gap: 8px;
                font-weight: 600;
                color: var(--project-text-primary);
                font-size: 15px;

                .task-icon {
                  color: var(--project-primary);
                  font-size: 20px;
                  width: 20px;
                  height: 20px;
                }

                span {
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                }
              }

              .task-meta {
                display: flex;
                align-items: center;
                gap: 12px;
                flex-wrap: wrap;

                mat-chip {
                  height: 24px;
                  font-size: 11px;
                  font-weight: 600;
                  color: white;
                  padding: 0 12px;
                }

                .task-dates {
                  font-size: 12px;
                  color: var(--project-text-secondary);
                }
              }
            }
          }
        }
      }

      // Right side - Timeline
      .timeline-container {
        flex: 1;
        display: flex;
        flex-direction: column;
        overflow-x: auto;
        overflow-y: hidden;
        position: relative;

        &::-webkit-scrollbar {
          height: 8px;
        }

        &::-webkit-scrollbar-track {
          background: var(--project-bg-secondary);
        }

        &::-webkit-scrollbar-thumb {
          background: var(--project-primary);
          border-radius: 4px;

          &:hover {
            background: var(--project-primary-dark);
          }
        }

        .timeline-header {
          height: 60px;
          background: var(--project-gradient-primary);
          border-bottom: 2px solid var(--project-primary-dark);
          overflow: hidden;

          .timeline-columns {
            display: flex;
            height: 100%;

            .timeline-column {
              display: flex;
              align-items: center;
              justify-content: center;
              border-right: 1px solid rgba(255, 255, 255, 0.2);
              color: white;
              font-weight: 600;
              font-size: 13px;
              text-align: center;

              &.today {
                background: rgba(255, 255, 255, 0.2);
                border-right: 2px solid white;
                border-left: 2px solid white;

                .column-label {
                  background: white;
                  color: var(--project-primary);
                  padding: 4px 12px;
                  border-radius: 12px;
                  font-weight: 700;
                }
              }
            }
          }
        }

        .timeline-body {
          flex: 1;
          position: relative;
          overflow-y: auto;

          &::-webkit-scrollbar {
            width: 8px;
          }

          &::-webkit-scrollbar-track {
            background: var(--project-bg-secondary);
          }

          &::-webkit-scrollbar-thumb {
            background: var(--project-primary);
            border-radius: 4px;

            &:hover {
              background: var(--project-primary-dark);
            }
          }

          .timeline-grid {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            display: flex;
            pointer-events: none;

            .grid-line {
              border-right: 1px solid var(--project-border-color);
              background: var(--project-bg-primary);

              &.today-line {
                background: rgba(255, 107, 53, 0.05);
                border-right: 2px solid var(--project-primary);
                border-left: 2px solid var(--project-primary);
              }
            }
          }

          .timeline-rows {
            position: relative;
            z-index: 1;

            .timeline-row {
              border-bottom: 1px solid var(--project-border-color);
              position: relative;

              .task-bar-wrapper {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                padding: 8px 0;

                .task-bar {
                  position: absolute;
                  border-radius: 8px;
                  cursor: pointer;
                  transition: all 0.3s ease;
                  box-shadow: 0 2px 8px var(--project-shadow);
                  overflow: hidden;

                  &:hover {
                    transform: translateY(-2px);
                    box-shadow: 0 4px 16px var(--project-shadow);
                    z-index: 10;
                  }

                  .task-bar-content {
                    position: relative;
                    width: 100%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    padding: 0 12px;

                    .task-bar-label {
                      color: white;
                      font-weight: 600;
                      font-size: 13px;
                      white-space: nowrap;
                      overflow: hidden;
                      text-overflow: ellipsis;
                      position: relative;
                      z-index: 2;
                    }

                    .task-bar-progress {
                      position: absolute;
                      top: 0;
                      left: 0;
                      bottom: 0;
                      background: rgba(255, 255, 255, 0.3);
                      transition: width 0.3s ease;
                      z-index: 1;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  // Legend
  .gantt-legend {
    margin-top: 20px;
    padding: 16px 20px;
    background: var(--project-bg-primary);
    border-radius: 12px;
    border: 1px solid var(--project-border-color);

    h3 {
      margin: 0 0 12px 0;
      font-size: 14px;
      font-weight: 700;
      color: var(--project-text-primary);
    }

    .legend-items {
      display: flex;
      gap: 24px;
      flex-wrap: wrap;

      .legend-item {
        display: flex;
        align-items: center;
        gap: 8px;

        .legend-color {
          width: 24px;
          height: 16px;
          border-radius: 4px;

          &.today-marker {
            background: repeating-linear-gradient(
              45deg,
              var(--project-primary),
              var(--project-primary) 4px,
              transparent 4px,
              transparent 8px
            );
            border: 2px solid var(--project-primary);
          }
        }

        span {
          font-size: 13px;
          color: var(--project-text-secondary);
          font-weight: 500;
        }
      }
    }
  }
}

// Responsive
@media (max-width: 1024px) {
  .gantt-container {
    padding: 16px;

    .gantt-header {
      flex-direction: column;
      gap: 16px;
      align-items: flex-start;

      .header-right {
        width: 100%;
        justify-content: space-between;
      }
    }

    .gantt-content .gantt-wrapper .task-list {
      width: 240px;
      min-width: 240px;
    }
  }
}

@media (max-width: 768px) {
  .gantt-container {
    .gantt-header {
      .header-left h1 {
        font-size: 24px;
      }

      .header-right {
        flex-direction: column;
        gap: 12px;

        .view-mode-buttons,
        .zoom-controls {
          width: 100%;
          justify-content: center;
        }
      }
    }

    .gantt-content .gantt-wrapper {
      flex-direction: column;

      .task-list {
        width: 100%;
        min-width: unset;
        border-right: none;
        border-bottom: 2px solid var(--project-border-color);
        max-height: 300px;
      }
    }
  }
}
